import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import { Button } from 'semantic-ui-react';
import styles from './Post.module.scss';

const Post = ({message, likes}) => {


  return (
    <div className={styles.postContainer} id="postContainer">
      <div className={styles.postAuthorInfo}>
        <div className={styles.avatar}><img src='https://user-life.com/uploads/posts/2018-08/1535602801_kak-delat-avatarki-krasivye-prikolnye-krutye-dlya-devushek-besplatno-foto-kartinki-dlya-vk-stima-yutuba-instagram-vatsapa.jpg'></img></div>
        <div className={styles.authorData}>
        <div className={styles.author}>Author Authorovich</div>
        <div className={styles.postDate}>13/13/2021</div>
        </div>
      </div>
      <div className={styles.postWrapper}>
      <div className={styles.postContent}>{message}</div>
      <div className={styles.line} />
      <div className={styles.likesWrapper}>
        <Button className={styles.likeButton}><FontAwesomeIcon icon={"heart"}
          transform="shrink-4.5"
          mask={["fas", "circle"]}
          className={styles.likeIcon} /></Button> <div className={styles.likeCount}>{likes}</div>
      </div>
      </div>
    </div>
  )
}

export default Post;