import React, { useState, useEffect, useCallback } from 'react';
import TextareaAutosize from 'react-textarea-autosize';
import { Button } from 'semantic-ui-react';
import useResizeObserver from '../../common/ResizeObserver/useResizeObserver';
import styles from './ProfileStatusWithHooks.module.scss'

const ProfileStatusWithHooks = ({status, updateStatus}) => {


    const [ref, { contentRect }] = useResizeObserver();

    const getHeight = useCallback(
      (key) => contentRect && Math.round(contentRect[key]),
      [contentRect]
    )
    const newHeight = getHeight('height')

    let [editMode, setEditMode ] = useState(false);
    let [newStatus, setStatus ] = useState(status);

    useEffect(()=>{
        setStatus(status)
    },[status])

    const activateEditMode = () => {
        setEditMode(true)
    }

    const deactivateEditMode = () =>  {
        setEditMode(false)

        updateStatus(newStatus)
    }

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)
    }

    return (
        <div id="infoStatus">
       { !editMode && 
            <span className={styles.status} onClick={activateEditMode}>{status || "-----"}</span>
        }
        {editMode &&
        <div className={styles.editMode}>
          <div className={styles.editStatus} style={{ height: `${newHeight}px` }}><TextareaAutosize ref={ref} autoFocus={true} onChange={onStatusChange} 
            value={newStatus}/></div>
            <Button className={styles.statusButton} onClick={deactivateEditMode}>Apply</Button>
        </div>
        }
        </div>
    )
    }

export default ProfileStatusWithHooks;