import React from 'react';
import { Form, Field} from 'react-final-form';
import { composeValidators, linkValidator, required } from '../../../utils/validators/validators';
import SocialContacts from '../SocialNetworck/SocialNetwork';
import arrayMutators from 'final-form-arrays';
import { Button } from 'semantic-ui-react';
import styles from './profileDataEditForm.module.scss';




const ProfileDataEditForm = React.memo((props) => {


    return (
        <Form
            initialValues={props.initialValues}
            onSubmit={props.onSubmit}
            subscription={props.subscription}
            mutators={{
                ...arrayMutators
            }}
            render={({ handleSubmit,
                submitError,
                form,
                submitting,
                pristine,
                values,form: {
                    mutators: { push, pop }
                  }
            }) => (
                <form id="profile-form" onSubmit={handleSubmit} >
                    <Field name="fullName">
                        {({ input, meta }) => (
                            <div className={ styles.fullName }>
                                <label>Full Name</label>
                                <input {...input} placeholder="Full Name"/>
                                {meta.touched && meta.error && <span>{meta.error}</span>}
                            </div>
                        )}
                    </Field>
                    <Field name="lookingForAJob">
                        {({ input, meta }) => (
                            <div className={styles.lookingForJob}>
                                <label>Looking for a job</label>
                                <input input={input} type="checkbox" />
                                {meta.touched && meta.error && <span>{meta.error}</span>}
                            </div>
                        )}
                    </Field>
                    <Field name="lookingForAJobDescription"  parse={value => (value ? value : "---")}>
                        {({ input, meta }) => (
                            <div className={styles.lookingForJobDescription}>
                                <label>My professional skills</label>
                                <input {...input} placeholder="My skills" type="textarea" />
                                {meta.touched && meta.error && <span>{meta.error}</span>}
                            </div>
                        )}
                    </Field>
                    <Field name="aboutMe" parse={value => (value ? value : "---")}>
                        {({ input, meta }) => (
                            <div className={styles.aboutMe}>
                                <label>About Me</label>
                                <input {...input} placeholder="About Me" type="textarea"/>
                                {meta.touched && meta.error && <span>{meta.error}</span>}
                            </div>
                        )}
                    </Field>
                    <Field name="contacts" editMode={props.editMode} initialValues={props.initialValues} contacts={props.profile.contacts} validate={composeValidators(linkValidator)}>
                        {props => <SocialContacts  {...props} validate={composeValidators(linkValidator)} subscription={props.subscription} />}

                    </Field> 
                         


                    <div className={styles.submitButton}>
                        <Button type="submit" disabled={submitting || pristine} errors={submitError}>Apply changes</Button>
                    </div>
                </form>
            )} />
    )
})

export default ProfileDataEditForm