import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useCallback, useRef } from 'react';
import { Field, Form } from 'react-final-form';
import { Button } from 'semantic-ui-react';
import { composeValidators, maxLengthCreator, required } from '../../../utils/validators/validators';
import { Textarea } from '../../common/Forms/formsControl';
import useResizeObserver from '../../common/ResizeObserver/useResizeObserver';
import styles from './MyPosts.module.scss';
import Post from './Post/Post';


let maxLength300 = maxLengthCreator(3000);

const MyNewPost = (props) => {

  const [ref, { contentRect }] = useResizeObserver();

  const getHeight = useCallback(
    (key) => contentRect && Math.round(contentRect[key]),
    [contentRect]
  )
  const newHeight = getHeight('height')

  const inputEl = useRef(null)
 
  return (
    <Form 
      onSubmit={props.onSubmit}
      subscription={props.subscription}
      validate={props.validate}
      render={({ handleSubmit,
        form,
        submitting,
        pristine,
        values
      }) => (
        <form onSubmit={handleSubmit} id="newPostForm"
          className={styles.form}>
          <div className={styles.inputArea} >
            <div className={styles.borderButton}>
              <Button className={styles.smilesButtonWrapper}>
                <FontAwesomeIcon
                  icon={["far", "grin-alt"]}
                  transform="shrink-1"
                  mask={["fas", "circle"]}
                  className={styles.buttonSmiles} />
              </Button>
            </div>
            <div className={styles.textWrapper} ref={inputEl}>
            <div className={styles.borderTextarea} style={{ height: `${newHeight}px` }} ></div>
            <Field name="myNewPost"
              component={Textarea} className={styles.postText}
              validate={props.validate}
              />
              </div>
          </div>
          <div className={styles.buttonWrapper}>
            <Button type="submit" className={styles.addPost} disabled={submitting || pristine}>Add post</Button>
          </div>
        </form>
      )}
    />
  )
}

const MyPosts = React.memo(({ postsData, addPost, ...props }) => {
  let postsElements = postsData.map((p, index = p.id) => 
  <div className={styles.post}>
    <Post message={p.message} likes={p.likesCount} key={index} />
    </div>);


  const onAddPost = (myNewPost) => {
    addPost(myNewPost);
  }

  const onSubmit = (values) => {
    onAddPost(values.myNewPost)
  }

  return (
    <div className={styles.postsBlock}>
      <h3>My posts</h3>
      <div className={styles.text}>
        <MyNewPost subscription={{ submitting: true, pristine: true }} onSubmit={onSubmit} validate={composeValidators(maxLength300, required)} props={props} />
      </div>
      <div className={styles.line}></div>
      <div className={styles.posts}>
        {postsElements}
      </div>
    </div>
  )
});

export default MyPosts;