import React from 'react';
import { Button } from 'semantic-ui-react';
import jobStatusOn from '../../../assets/images/red-v.png';
import jobStatusOff from '../../../assets/images/red-x.png';
import SocialContacts from '../../common/SocialNetworck/SocialNetwork';
import styles from './ProfileData.module.scss'

const ProfileData = ({ profile, isOwner, goToEditMode, editMode }) => {
    const lookingStatus = (profile.lookingForAJob === true ? jobStatusOn : jobStatusOff);
    return (
        <div id="profileData">
            <div className={styles.dataComponent}>
                <span className={styles.dataInfo}><b>Full name:</b> {profile.fullName}</span>
            </div>
            <div className={styles.dataComponent}>
            <span className={styles.dataInfo}><b>Looking for a job:</b> <img alt="looking for a job" src={lookingStatus} /></span>
            </div>
            <div className={styles.dataComponent}>
            <span className={styles.dataInfo}><b>My professional skills:</b> {profile.lookingForAJobDescription}</span>
            </div>
            <div className={styles.dataComponent}>
            <span className={styles.dataInfo}><b>About me:</b> {profile.aboutMe}</span>
            </div>
            <div className={styles.dataComponentContacts}>
                <span className={styles.contacts}><b>Contacts:</b></span>
                <div className={styles.componentContact}>{Object.keys(profile.contacts).map(key => {
                return <SocialContacts  key={key} contactTitle={key} contactValue={profile.contacts[key]} editMode={editMode} contacts={profile.contacts}/>
            })}
                </div>
            </div>
            {isOwner && <Button onClick={goToEditMode} className={styles.editButton}>Edit</Button>}
        </div>
    )
}

export default ProfileData