import React from 'react';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import MyPostsContainer from './MyPosts/MyPostsContainer';
import style from './Profile.module.scss'




const Profile = ({ profile, status, updateStatus, store, isOwner, savePhoto, editMode, saveProfile }) => {

  return (
    <div className={style.profile}>
      <div className={style.leftWrapper}>
        <ProfileInfo className={style.info} savePhoto={savePhoto} isOwner={isOwner}
          profile={profile} status={status}
          updateStatus={updateStatus}
          editMode={editMode}
          saveProfile={saveProfile} />
      </div>
      <div className={style.rightWrapper}>
        <MyPostsContainer className={style.posts} store={store} />
      </div>
    </div>
  );
}

export default Profile;