import React from 'react';
import { Field } from 'react-final-form';
import MainLink from './../../../assets/images/MainLink.svg'
import { FieldArray } from 'react-final-form-arrays'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Image, Table } from 'semantic-ui-react';
import styles from './SocialNetwork.module.scss'


const SocialContact = ({contacts, editMode, contactTitle, contactValue, validate, initialValues }) => {
    const links ={ 'facebook' : 'facebook',
                    'website' :  'link',
                    'vk' : 'vk',
                    'twitter' : 'twitter',
                    'instagram' : 'instagram',
                    'youtube' : 'youtube',
                    'github' : 'github',
                    'mainLink' : 'MainLink' }

                const link = Object.entries(links).find(([key, value]) => {
                  return key === contactTitle ? {value} :   false
                })
    if (!editMode) {
        if(link[1] !== 'MainLink' && link[1] !== 'link'){
            if(contactValue){
        return (
             <div className={styles.linkContact}>
                <FontAwesomeIcon className={styles.icons} icon={['fab', link[1]]} />
                </div> 
        )}else { 
            return (
            <div className={styles.linkContact}><a href={contactValue}>
               <FontAwesomeIcon className={styles.icons} icon={['fab', link[1]]} /></a>
               </div>
               )}
               } else if(link[1] === 'link') {
            return (
            <div className={styles.linkContact}><a href={contactValue}>
                <FontAwesomeIcon className={styles.icons} icon={['fas', link[1]]} /></a>
                </div>
            )
        } else {
            return <div className={styles.linkContact}><a href={contactValue}>
                <Image src={MainLink} verticalAlign='middle' id="mainLink" /></a>
                    </div>
        }
    } else {
        const contactArray = Object.keys(contacts).map(key => {
            return key
        })
        return (
            <FieldArray name="contacts" fields={contactArray}>
                {({ fields, subscription }) => (
                    <Table>
                        <Table.Body className={styles.contacts}>
                            {fields.map((name, index) => (
                                <Table.Row className={styles.editContact}>
                                    <Table.Cell width={2} className={styles.contactName}>{name}</Table.Cell>
                                    <Table.Cell className={styles.linkEditContact}>
                                        <Field name={`contacts.${name}`} component="input" validate={validate} subscription={subscription} index={index}>
                                            {({ input, meta }) => (
                                                <div className={styles.linkEditInput}>
                                                    <input {...input} placeholder={name} subscription={subscription} />
                                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                                </div>
                                            )}
                                        </Field>
                                    </Table.Cell>
                                </Table.Row>
                            ))}
                        </Table.Body>
                    </Table>
                )}
            </FieldArray>
        )
    }
}


const SocialContacts = (props) => {

    return <div className={styles.socialContact}><SocialContact {...props} /></div>
}

export default SocialContacts;